import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { signin } from '../actions/userAct';
import LoadingBox from '../components/LoadingBox';
import MessageBox from '../components/MessageBox';

export default function SigninPage(props) {

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState('');

    const redirect = props.location.search ? props.location.search.split('=')[1] : '/';

    const userSignin = useSelector((state) => state.userSignin);
    const { userInfo, loading, error  } = userSignin;

    const dispatch = useDispatch();

    const submitHandler = (e) => {
        e.preventDefault();
        dispatch(signin(email, password));
    }
    useEffect(() => {
        if (userInfo) {
            props.history.push(redirect);
        }
    }, [props.history, redirect, userInfo]);
    return (
        <div>
            <form className="form" onSubmit={submitHandler}>
                <div>
                    <h1>התחברות</h1>
                </div>
                {loading && <LoadingBox></LoadingBox>}
                {error && <MessageBox variant="danger">{error}</MessageBox>}
                <div>
                    <label htmlFor="email"> אימייל </label>
                    <input
                        type="email"
                        id="email"
                        placeholder="הכניסו את האימייל פה"
                        required
                        onChange={e => setEmail(e.target.value)}></input>
                </div>
                <div>
                    <label htmlFor="password"> סיסמה </label>
                    <input
                        type="password"
                        id="password"
                        placeholder="הכניסו את הסיסמה פה"
                        required
                        onChange={e => setPassword(e.target.value)}></input>
                </div>
                <div>
                    <label />
                    <button className="primary" type="submit"> אשר </button>
                </div>
                <div>
                    <label />
                    <div>
                        לקוח חדש? {' '}<Link to={`/register?redirect=${redirect}`}>פתחו חשבון</Link>
                    </div>
                </div>
            </form>
        </div>
    )
}
